import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login from '@/components/Login'
import DataA from '@/components/DataA'
import SgList from '@/components/SgList'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: DataA
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/dataA/:id',
      component: DataA,
      children: [
        {
          path: 'sgList',
          component: SgList
        }
      ]
    }
  ]
})



